<template>
  <div class="container">
    <div class="map" id="map"></div>
    <div class="nav">
      <div class="nav-item" :class="{active:active===index}" @click="onClick(index)" v-for="(item,index) in nav" :key="index">{{item.name}}</div>
    </div>
  </div>
</template>

<script>
import { initCesiumToken, CreateMap } from '../utils/map'
import 'cesium/Build/Cesium/Widgets/widgets.css'
export default {
  name: 'layout',
  data () {
    return {
      nav: [
        { name: '地图', key: '0'},
        { name: '列表', key: '1'},
        { name: '我的', key: '2'},
        { name: '商城', key: '3'},
        { name: '其他', key: '4'}
      ],
      active: 2
    }
  },
  props: {
  },
  computed: {
    
  },
  watch: {
  },
  created () {
    initCesiumToken()
  },
  mounted () {
    this.init()
  },
  beforeDestroy () {
  },
  methods: {
    init() {
      this.map = CreateMap('map')
    },
    onClick (inx) {
      if (this.active === inx) return
      this.active = inx
    }
  }
}
</script>
